import b2bFooter from "../../components/b2bfooter.vue"; 
export default{
	components:{
		b2bFooter
	}, 
	data: function() {
		return {
			pageData: {},
			pageLoad:false,
		}
	},
	created: function() {
		this.getPage();
	},
	methods: {
		getPage: function() {
			var that = this;
			uni.request({
				url: that.app.apiHost+"/module.php?m=b2b_cart&ajax=1",
				data:{
					authcode:that.app.getAuthCode()
				},
				dataType: "json",
				success: function(rs) {
					var res=rs.data;
					if(res.error==1000){
						uni.showToast({
							title:"请先登录"
						})	
						return false;
					}
					that.pageLoad=true;
					that.pageData = res.data;
				}
			})
		},
		plusCart: function(id, amount, ksid) {
			var that = this;
			var productid = id;
			var amount = amount;
			var ksid = ksid == undefined ? 0 : ksid;
			amount++;
			uni.request({
				url: that.app.apiHost+'/module.php?m=b2b_cart&a=add&ajax=1',
				data: {
					productid: productid,
					amount: amount,
					ksid: ksid,
					authcode:that.app.getAuthCode()
				},
				method: 'GET',
				dataType: "json",

				success: function(res) {
					that.getPage();
				}
			})
		},
		minusCart: function(id, amount, ksid) {
			var that = this;
			var productid = id;
			var amount = amount;
			var ksid = ksid == undefined ? 0 : ksid;
			amount--;
			var isdelete = 0;
			if (amount == 0) {
				isdelete = 1
			}
			uni.request({
				url: that.app.apiHost+'/module.php?m=b2b_cart&a=add&ajax=1',
				data: {
					productid: productid,
					amount: amount,
					ksid: ksid,
					isdelete: isdelete,
					authcode:that.app.getAuthCode()
				},
				method: 'GET',
				dataType: "json",

				success: function(res) {
					that.getPage();
				}
			})
		},
		goConfirm:function(shopid){
			uni.navigateTo({
				url:"../b2b_order/confirm?shopid="+shopid
			})
		},
		goShop:function(shopid){
			uni.navigateTo({
				url:"../b2b_shop/index?shopid="+shopid
			})
		} 
	}
};

